import React from 'react';


import Card from '@material-ui/core/Card';
import CardHeader from '@material-ui/core/CardHeader';
import CardActions from '@material-ui/core/CardActions';

import IconButton from '@material-ui/core/IconButton';
import { red } from '@material-ui/core/colors';
import FavoriteIcon from '@material-ui/icons/Favorite';
import ShareIcon from '@material-ui/icons/Share';
import MoreVertIcon from '@material-ui/icons/MoreVert';


import Button from '@material-ui/core/Button';
import Avatar from '@material-ui/core/Avatar';
import SubjectIcon from '@material-ui/icons/Subject';
import { useGenericModel, useSnackbarsAlert, useBackdrop } from '../../hooks';

import {
    Accordion,
    AccordionSummary,
    AccordionDetails,
    ListItemText,
    ListItemAvatar,
  } from '@material-ui/core';

import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import EditIcon from '@material-ui/icons/Edit';

import { Calendar, momentLocalizer } from 'react-big-calendar'
import moment from 'moment'

import { 
  WrapperButton,
  useStyles,
} from './styles';

const localizer = momentLocalizer(moment)

const Agenda = () => {

  const [eventos, setEventos, EventosInicial, addEventos, rmEventos, putEventos, upEventos] = useGenericModel('account_events');
  const [listEvents, setListEvents] = React.useState([])

  React.useEffect(()=>{
      criaListaEventos(eventos)
  },[eventos])

  function criaListaEventos(eventos){
      let lista = eventos.map( evento => {
            let start = new Date(evento.dia)
            let [hours, minute] = evento.servico.tempo_atividade.split(":")
            let end = new Date( moment(start).add(hours, 'hours').add(minute, 'minute').format() )

            return {   
              title: `${evento.servico.nome} - ${evento.cliente.first_name}`,
              start: new Date(start.getFullYear(), start.getMonth(), start.getDate(), start.getHours() + 1, start.getMinutes()),
              end: new Date(end.getFullYear(), end.getMonth(), end.getDate(), end.getHours() + 1, end.getMinutes()),
              allDay: false,
              resource: [],
              evento: evento
          }
      })
    setListEvents(lista)
  }

  const customSlotPropGetter = date => {
      return {
        style: {
            height: '200px',
        },
      }
  }


  return (
    <div>
        <Calendar
            localizer={localizer}
            events={listEvents}
            startAccessor="start"
            endAccessor="end"
            style={{ height: '82vh' }}
            defaultView="day"
            scrollToTime={new Date()}
            toolbar={true}
            slotPropGetter={customSlotPropGetter}
            eventPropGetter={customSlotPropGetter}
            // dayPropGetter={customSlotPropGetter}
            popup={false}
            components={{
              event: EventCard,
            }}
             />
    </div>
  );
}

function EventCard({event}) {

  const classes = useStyles();
  const {evento} = event

  return (
    <Card className={classes.rootEvent}  >
      <CardHeader
        avatar={
          <Avatar aria-label="recipe" className={classes.avatar}  src={evento.cliente.cliente?.avatar} >
              {evento.cliente?.first_name }
          </Avatar>
        }
        action={
          <IconButton aria-label="settings">
            <MoreVertIcon />
          </IconButton>
        }
        title={`${evento.servico?.nome} - ${evento.cliente?.first_name }`}
        subheader={`${ moment(new Date(evento?.dia)).format("DD/MM/YYYY") } - ${ moment(new Date(evento?.dia)).format('H:mm') }`}
      />
      <CardActions disableSpacing>
        <IconButton aria-label="add to favorites">
          <FavoriteIcon />
        </IconButton>
        <IconButton aria-label="share">
          <ShareIcon />
        </IconButton>
      </CardActions>
    </Card>
  );
}

export { Agenda }
